import React from 'react'
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import SearchIcon from '@mui/icons-material/Search';

export default function SearchBar() {
  return (
    <form onSubmit={() => {}} className='mt-2 relative w-full lg:w-[700px] flex items-center'>
      <Input type='search' placeholder='SteamID'
      className='text-slate-200 placeholder-slate-50 rounded-r-none focus-visible:ring-0 focus-visible:ring-transparent focus-visible:ring-offset-0'
      />
      <Button type='submit' size='icon' variant='secondary' className='rounded-l-none'>
        <SearchIcon className='h-5 w-5 text-muted-foreground'/>
      </Button>
    </form>
  )
}
